
import { useOrganizationStore, useResourceStore, useUserStore } from '@/store';
import { ResourceCard } from '@/components';
import { defineComponent, ref } from 'vue';
import { useRoute } from 'vue-router';

export default defineComponent({
  name: 'SearchPage',
  components: {
    ResourceCard,
  },
  props: {
    q: { type: String },
  },
  setup(state) {
    const userStore = useUserStore();
    const route = useRoute();
    const query = route.query.q;

    if (typeof query !== 'string' && query) {
      query?.toString();
    }

    console.log(typeof query);
    console.log(query);

    const resourceStore = useResourceStore();
    const organizationStore = useOrganizationStore();
    async function loadAllData() {
      await organizationStore.loadData();
      await resourceStore.loadData();
    }
    loadAllData();

    const resourceSearchResults = ref(resourceStore.userSearchResults());

    console.log(resourceSearchResults.value);

    return {
      resourceStore, resourceSearchResults, userStore,
    };
  },
});
